// Font-face

@if variable-exists(external-fonts) {
  @each $url in $external-fonts {
    @import url($url);
  }
}

@if variable-exists(typefaces) {
  @each $file, $option in $typefaces {
    @font-face {
      font-family: map-get(map-get($typefaces, $file), family);
      font-style: map-get(map-get($typefaces, $file), style);
      font-weight: map-get(map-get($typefaces, $file), weight);
      src: url('../fonts/#{$file}/#{$file}.eot'); /* IE9 Compat Modes */
      src: local(map-get(map-get($typefaces, $file), family)), local('#{$file}'),
        url('../fonts/#{$file}/#{$file}.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
        url('../fonts/#{$file}/#{$file}.woff2') format('woff2'), /* Super Modern Browsers */
        url('../fonts/#{$file}/#{$file}.woff') format('woff'), /* Modern Browsers */
        url('../fonts/#{$file}/#{$file}.ttf') format('truetype'), /* Safari, Android, iOS */
        url('../fonts/#{$file}/#{$file}.svg##{$file}') format('svg'); /* Legacy iOS */
    }
  }
}
