// Typography

//* Base

$base-font-size: 15;
$base-line-height: 1.5;

//* Font families

// $typefaces
//
// Configuration of webfonts imports.
//
// $typefaces - {map}
// @family - {string}
// @weight - {integer} or {keyword} i.e. 800, bold
// @style - {keyword} i.e. normal, italic

$typefaces: (
  'saira-extra-condensed-v4-latin-regular': (
    family: 'Saira Extra Condensed',
    style: normal,
    weight: 400,
  ),
  'Rajdhani-Regular': (
    family: 'Rajdhani Reg',
    style: normal,
    weight: 400,
  ),
  'Rajdhani-SemiBold': (
    family: 'Rajdhani SemiBold',
    style: normal,
    weight: 500,
  ),
  'Rajdhani-Bold': (
    family: 'Rajdhani Bold',
    style: normal,
    weight: 700,
  ),
);

$external-fonts: ();

$font-family-sans: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;

$font-family-serif: 'Georgia', 'Cambria', 'Times New Roman', 'Times', serif;

$font-family-monospace: 'Inconsolata', 'Menlo', 'Consolas', 'Bitstream Vera Sans Mono', 'Courier', monospace;

$font-family-saira: 'Saira Extra Condensed', 'Helvetica', sans-serif;

$font-family-rajdhani-reg: 'Rajdhani Reg', 'Helvetica', sans-serif;

$font-family-rajdhani-bold: 'Rajdhani Bold', 'Helvetica', sans-serif;

$font-family-default: $font-family-rajdhani-reg;

//* Font sizes

// Desktop

// Devices

//* Line heights

//* Styles

body {
  font-size: 16px;
  font-size: 115.625%; // 1rem should equal 18.5px
}

@mixin h1-title($setColor, $set980TextIndent, $setLetterSpacing, $set400TextIndent) {
  font-size: 5em;
  font-weight: normal;
  margin: 0;
  text-indent: 20px;
  letter-spacing: 20px;
  padding-bottom: 0px;
  line-height: 1;

  @media only screen and (min-width: 1920px) {
    font-size: 6.25em;
    line-height: 1;
  }

  @media only screen and (max-width: 980px) {
    font-size: 3em;
    text-indent: $set980TextIndent;
    letter-spacing: $setLetterSpacing;
    line-height: 1;
  }

  @media only screen and (max-width: 400px) {
    font-size: 3em;
    line-height: 1;
    text-indent: $set400TextIndent;
    letter-spacing: 5px;
  }
}

@mixin h1-sairabold($setColor) {
  font-family: $font-family-saira;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1;
  font-size: 3em;
  color: $setColor;

  @media only screen and (min-width: 1920px) {
    font-size: 3.75em;
    line-height: 1;
  }

  @media only screen and (max-width: 980px) {
    font-size: 2.375em;
    line-height: 1;
  }

  @media only screen and (max-width: 400px) {
    font-size: 2em;
    line-height: 1;
  }
}

@mixin h1-span-title($setColor) {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
  text-indent: 20px;
  letter-spacing: 20px;
  padding-bottom: 0px;
  line-height: 1;
}

@mixin h1-span-sairabold($setColor) {
  font-family: $font-family-saira;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1em;
  font-size: 1em;
  color: $setColor;
}

@mixin h2-rajbold($setColor) {
  font-family: $font-family-rajdhani-bold;
  line-height: 44px;
  font-size: 1.95rem;
  color: $setColor;
  font-weight: 700;

  @media only screen and (max-width: 980px) {
    line-height: .864em;
  }

  @media only screen and (max-width: 400px) {
    font-size: 1.5em;
    line-height: 1.167em;
  }
}

@mixin h2-span-rajbold($setColor) {
  font-family: $font-family-rajdhani-bold;
  line-height: 1em;
  font-size: 1em;
  color: $setColor;
  font-weight: 700;
}

@mixin h3-rajreg($setColor, $setFontSize: 1.6rem) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: $setFontSize;
  font-weight: 400;
  line-height: 1.28em;
}

@mixin h3-span-rajreg($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: 1em;
  font-weight: 400;
  line-height: 1em;
}

@mixin h4-rajreg($setColor, $setLineHeight) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: 1.25em;
  font-weight: 500;
  line-height: $setLineHeight;
}

@mixin h4-span-rajreg($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: 1.25em;
  font-weight: 500;
  line-height: 1em;
}

@mixin h5-rajsemi($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: 1.1em;
  font-weight: 700;
  line-height: 1.5em;
}

@mixin h5-span-rajsemi($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: 1.1em;
  font-weight: 700;
  line-height: 1em;
}

@mixin h6-rajsemi($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-bold;
  font-size: 1em;
  font-weight: 700;
  line-height: 1.5em;
}

@mixin h6-span-rajsemi($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-bold;
  font-size: 1em;
  font-weight: 700;
  line-height: 1em;
}

@mixin p-rajreg($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: 1em;
  font-weight: 500;
  line-height: 1.45rem;
}

@mixin p-span-rajreg($setColor) {
  color: $setColor;
  font-family: $font-family-rajdhani-reg;
  font-size: 1em;
  font-weight: 500;
  line-height: 1em;
}

#page-container,
#sidebar-overlay {
  h1,
  h2,
  h3,
  h4 {
    margin-bottom: 20px;
  }

  h1 {
    margin-top: 10px;
    @include h1-sairabold($hypr-dark-blue);

    span {
      @include h1-span-sairabold($hypr-dark-blue);
    }
  }

  h1.title {
    @include h1-title($white, 10px, 10px, 5px);

    span {
      @include h1-span-title($white);
    }
  }

  h2 {
    @include h2-rajbold($hypr-dark-blue);

    span {
      @include h2-span-rajbold($hypr-dark-blue);
    }
  }

  h3 {
    @include h3-rajreg($hypr-dark-blue, 1.563em);

    span {
      @include h3-span-rajreg($hypr-dark-blue);
    }
  }

  h4 {
    @include h4-rajreg($hypr-dark-blue, 1.5em);

    span {
      @include h4-span-rajreg($hypr-dark-blue);
    }
  }

  h5 {
    @include h5-rajsemi($hypr-dark-blue);

    span {
      @include h5-span-rajsemi($hypr-dark-blue);
    }
  }

  h6 {
    @include h6-rajsemi($hypr-dark-blue);

    span {
      @include h6-span-rajsemi($hypr-dark-blue);
    }
  }

  p {
    @include p-rajreg($hypr-dark-blue);

    span {
      @include p-span-rajreg($hypr-dark-blue);
    }
  }

  .entry-content {
    p a {
      color: #9074b5;
      font-weight: bold;

      &:hover {
        text-decoration: underline;
      }
    }

    p {
      margin: 10px 0;

      strong {
        font-weight: bold;
      }
    }
  }
}

