// Buttons

.et_pb_button {
  margin-top: 20px;
  margin-bottom: 20px;

    @include h5-rajsemi($white);
    @include border-radius(4px!important);
    @include transition(.2s all ease-in-out!important);

    padding: 6px 3em 8px 3em!important;
    line-height: 27px!important;
    text-transform: capitalize;
    min-width: 180px;
    text-align: center;

   &:after {
    content: "\45";
    font-size: 21px;
    line-height: 25px;
    margin-left: -8px;
    margin-top: 0px;
    transition: all 300ms ease 0ms;
   }

    &:hover {
      text-decoration: underline;

      &:after {
        margin-left: 8px;
      }
    }

    &.et_pb_bg_layout_dark {
        @include h5-rajsemi($white);
        background-color: $hypr-teal!important;
        border: 1px solid #92EBF0!important;
        @include box-shadow(0 0 5px $hypr-teal);
        @include border-radius(3px!important);
    }

    &.et_pb_bg_layout_light {
        @include h5-rajsemi($white);
        background-color: $hypr-dark-blue!important;
        border: 1px solid $hypr-dark-blue!important;
        @include box-shadow(0 0 0px $hypr-dark-blue);
        @include border-radius(3px!important);
    }

    // we are wrapping input elements to enable the use of ::after (pseudo-element)
    // the end effect is to copy the hover effect in place for <a> tags
    // we can't use ::after on <input> directly: https://stackoverflow.com/a/4660434/605748
    // also see: frmamework js
    .hbspt-form & > input.hs-button,
    & > input {
      border: 0 none !important;
      padding: 0 !important;
    }
}

span.et_pb_button::after {
  margin-top: 10px;
}
