//Homepage Exceptions

.page-id-119 #page-container {
  .et_pb_section.et_pb_section_first {
    h1:first-child {
      @include h1-title($white, 20px, 10px, 10px);

      span {
        @include h1-title($white, 20px, 10px, 10px);
      }
    }
  }
}

.hover-image-desc {
  display: block;
  @include transition(.2s all ease-in-out);
  max-height: 683px;

  &:hover {
    display: block;
    @include transition(.2s all ease-in-out);

    .et_pb_text_inner {
      @include transition(.2s all ease-in-out);
      @include transform(translateY(-100%));
    }
  }

  .et_pb_text_inner {
    @include transition(.2s all ease-in-out);
    @include transform(translateY(-14px));
  }
}

//Logo Slider

.slick-list {
  padding: 20px 0 !important;
}

.wpls-logo-showcase .wpls-logo-cnt.slick-slide {
  margin: 0 10px;
}

.wpls-fix-box {
  height: 100%;
  padding: 20px;

  img {
    height: 100%;
    object-fit: contain;
  }
}

.wpls-logo-showcase {
  padding: 0 !important;
}

.wpls-logo-showcase .slick-dots {
  display: none !important;
}

.wpls-logo-showcase button.slick-arrow {
  @include box-shadow(0 0 10px rgba(0, 0, 0, .3) !important);
  @include border-radius(100px !important);
  background-image: none !important;
  background-color: $white !important;
  width: 40px;
  height: 40px;
  overflow: hidden;

  &.slick-prev {
    @include transform(translateX(-45px));

    &:after {
      background-image: url("../images/global/prev-arrow.svg");
      background-repeat: no-repeat;
      background-position: 14px 13px;
    }
  }

  &.slick-next {
    @include transform(translateX(45px));

    &:after {
      background-image: url("../images/global/next-arrow.svg");
      background-repeat: no-repeat;
      background-position: 16px 13px;
    }
  }

  &:after {
    content: "";
    text-indent: 0px;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;

    @include transform(translate(-50%, -50%));
    color: black;
    width: 100%;
    height: 100%;
    text-align: center;
  }

  &:hover,
  &:active,
  &:focus {
    background-color: transparent !important;
    @include transition(.2s all ease-in-out);
    @include box-shadow(0 0 10px rgba(0, 0, 0, .6) !important);
  }
}

.wpls-logo-cnt {
  @include box-shadow(0 0 10px rgba(0, 0, 0, .3));
  @include border-radius(4px);
  position: relative;

  .wp-post-image {
    position: absolute;
    left: 50%;
    top: 50%;
    padding: 10px !important;
    @include transform(translate(-50%, -50%));
    width: auto !important;
    height: 100%;
    object-fit: contain;
  }

  @media only screen and (min-width: 2000px) {
    height: 250px !important;
  }

  @media only screen and (min-width: 1600px) {
    height: 150px !important;
  }

  @media only screen and (max-width: 1600px) {
    height: 130px !important;
  }

  @media only screen and (max-width: 1400px) {
    height: 60px !important;
  }

  @media only screen and (max-width: 1280px) {
    height: 150px !important;
  }

  @media only screen and (max-width: 1040px) {
    height: 200px !important;
  }
}

.table-wrap {
  width: 100%;
  overflow-x: scroll;
  border-right: 1px solid rgba(0, 0, 0, .1);
}

div.pdfemb-inner-div {
  display: block !important;
}

.pdfemb-viewer,
.pdfemb-pagescontainer {
  height: 100% !important;
  background-color: transparent !important;
}

div.pdfemb-inner-div {
  display: block !important;
  background-color: transparent !important;
}
