// Footer

#custom-footer {
	p {
		color: $white!important;

    span {
			color: $white!important;
		}

		a {
			font-weight: normal!important;
			color: $white!important;
		}
	}

	.hs_error_rollup {
		display: none;
	}

	.hbspt-form form .hs-fieldtype-text {
		display: block;

		@media only screen and (max-width: 980px){
			width: 75%;
		}

		@media only screen and (max-width: 480px){
			width: 100%;
		}
	}

	.hs-button {
		margin-top: -10px!important;
	}
}

#main-footer {
  display: none;
}
