//Section Specific Modules
#s4 {
  .et_pb_column_3_5 {
    @media only screen and (max-width: 980px) {
      padding: 200px 60px 0px 60px;
    }
  }

  .et_pb_column_2_5 {
    @media only screen and (max-width: 980px) {
      padding-left: 0px;
      padding-right: 0px;
    }
  }
}

#s5 {
  padding-bottom: 80px !important;
}

.et_pb_section_first,
.et_pb_section_0 {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
  background-color: $hypr-mid-blue-2 !important;

  @media only screen and (max-width: 980px) {
    .et_pb_row_0.et_pb_row {
      margin: 0 !important;
    }
  }
}

body.home {
  .et_pb_section_first {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}

#s9,
#s10,
#s11,
#s12 {
  padding: 0px !important;
}

//Slider Styles
.et_pb_slider {
  @media only screen and (max-width: 980px) {
    max-width: 100% !important;

    .et_pb_container {
      padding-top: 60px;
    }
  }
}

//Masthead Styles

.masthead-section {
  .et_pb_image,
  .et_pb_button {
    margin-top: 20px;
  }
}

//Media Styles

.et_pb_video_play {
  background-color: $white;
  @include border-radius(100px);
  height: 60px;
  width: 60px;
  overflow: hidden;
  font-size: 52px !important;
  line-height: 52px !important;
  margin: 0 !important;
  border: 4px solid $hypr-pink;
  @include box-shadow(0 0 20px $hypr-pink);
  @include transform(translateX(-30px)translateY(-30px));

  &:before {
    margin-left: 0px;
    display: block;
    color: $hypr-pink;
    @include transform(translateY(0px)translateX(2px));
    content: "\45";
  }
}

@media only screen and (min-width: 768px) {
  #et-info {
    float: right !important;
  }
}

.et_pb_image {
  overflow: hidden !important;
}

.et_pb_image_wrap {
  height: 100% !important;
  display: block;
  width: 100% !important;
  background-color: transparent;
  overflow: hidden;
  position: relative;
}

.et_pb_video {
  height: 100%;
}

.et_pb_row.et_pb_row_fullwidth,
.et_pb_specialty_fullwidth>.et_pb_row {
  width: 100% !important;
  max-width: 100% !important;
}

@media only screen and (max-width: 980px) {
  .et_pb_row.et_pb_row_fullwidth,
  .et_pb_specialty_fullwidth>.et_pb_row {
    padding: 0px 40px !important;
    padding-left: 40px !important;
    padding-right: 40px !important;
    margin: 0px !important;
  }
}

@media (min-width: 981px) {
  .et_pb_gutters3 .et_pb_column_1_2,
  .et_pb_gutters3.et_pb_row .et_pb_column_1_2 {
    margin-right: 0px;
    width: 50%;
  }
}

@media only screen and (max-width: 1280px) {
  .et_pb_module.et_pb_text {
    max-width: 100%;
  }

  .et_pb_section .et_pb_row {
    padding-left: 40px !important;
    padding-right: 40px !important;
    margin: 0 auto !important;
    width: 100% !important;
  }

  #sidebar-overlay {
    .et_pb_section .et_pb_row {
      padding-left: 0px !important;
      padding-right: 0px !important;
    }
  }

  #s-1 {
    padding: 0 !important;

    .content-col {
      padding: 60px 60px !important;
    }
  }

  .et_pb_column.content-col {
    background-image: none;
  }
}

/* ipad fix */
@media only screen and (min-device-width: 1024px) and (max-device-width: 1366px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
  .ipad-row-fix.et_pb_row {
    padding-left: 0px !important;
    padding-right: 0px !important;
  }
}

/*bio page mobile fix */
.mobile-row.et_pb_row {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

@media only screen and (max-width: 400px) {
  .et_pb_section .et_pb_row {
    padding: 25px !important;
    margin: 0 !important;
    width: 100% !important;

    .et_pb_column {
      margin: 0 !important;
    }
  }

  #s-1 {
    padding: 0px !important;

    .content-col {
      padding: 60px 60px !important;
    }
  }
}

.wpls-logo-showcase.center .slick-center {
  @include transform(scale(.7, .7) !important);
}

.wpls-logo-slider.center .slick-slide {
  opacity: 1 !important;
}

//Overlay Styles

.overlay-container .overlay-close {
  background-color: $white;
  border: 2px solid $hypr-pink;
  @include box-shadow(0 0 10px $hypr-pink);
  height: 50px;
  width: 50px;
  padding: 0;
  @include border-radius(50px);

  span {
    font-size: 34px;
    position: absolute;
    top: 50%;
    margin-top: -4px;
    display: block;
    left: 50%;
    color: $hypr-pink;
    @include transform(translate(-50%, -50%));
  }
}

.overlay-container {
  .et_pb_row_7.et_pb_row {
    padding-top: 0px !important;
  }
}

#et-main-area {
  overflow-x: hidden;
}

#page-container {
  .et-pb-contact-message {
    display: block;

    p,
    ul,
    li {
      color: $white !important;
    }
  }
}

#page-container .et_pb_contact {
  @media only screen and (max-width: 640px) {
    margin: 60px 0 !important;
  }

  p .et_contact_error {
    border: none !important;
    border-bottom: 1px solid $hypr-pink !important;
  }

  form {
    .et_pb_contact_field {
      display: inline-block;
      width: 50%;
      vertical-align: top;
      margin: 0 -4px 40px 0;

      span {
        display: none;
      }

      input {
        width: 100%;
        color: $white;
        background-color: transparent;
        border-bottom: 1px solid $white;

        &::placeholder {
          color: $white;
        }
      }
    }
  }

  .hs-input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, 1);
    outline: none;
    display: block;
  }

  .hs-error-msgs {
    background-color: black;
    @include border-radius(4px);
    border: 1px solid $hypr-pink;
    padding: 5px;
    margin: 10px 10px 10px 0;
    list-style-type: none;

    label {
      color: $hypr-pink;
      @include h5-rajsemi($hypr-pink);
      font-size: 12px !important;
    }
  }

  .et_pb_contact_submit {
    margin-top: 10px;
    @include h5-rajsemi($white);
    @include border-radius(2px);
    background-color: $hypr-teal !important;
    padding: 5px 20px;
    border: 1px solid #92EBF0 !important;
    @include box-shadow(0 0 5px $hypr-teal);
  }
}
